import React, { Component } from "react";
import logo from "../images/logo_.svg";
import { Link } from "react-router-dom";

export class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      isSticky: false
    };
  }

  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen
    });
  };

  sticky = () => {
    if (window.scrollY > 0) {
      this.setState({
        isSticky: true
      });
    } else {
      this.setState({
        isSticky: false
      });
    }
  };

  componentDidMount() {
    window.addEventListener("resize", () => {
      this.setState({
        isOpen: false
      });
    });
    window.addEventListener("scroll", this.sticky);
  }

  render() {
    return (
      <div className={!this.state.isSticky ? "navbar" : "navbar sticky"}>
        <div className="container">
          <a href="/thegetfit">
            <img src={logo} alt="" className="logo" />
          </a>
          <ul
            className={!this.state.isOpen ? "nav" : "nav navbar-nav closeHam"}
          >
            <Link to="/thegetfit">
              <li>
                <a href="/thegetfit">Home</a>
              </li>
            </Link>

            <Link to="/about">
              <li>
                <a href="about">About</a>
              </li>
            </Link>

            <Link to="/offers">
              <li>
                <a href="offers">offers</a>
              </li>
            </Link>

            <Link to="/plans">
              <li>
                <a href="plans">plans</a>
              </li>
            </Link>

            <Link to="/trainers">
              <li>
                <a href="trainers">trainers</a>
              </li>
            </Link>

            <Link to="/contact">
              <li>
                <a href="contact">contact</a>
              </li>
            </Link>
          </ul>
          <div
            className={!this.state.isOpen ? "hamberger" : " hamberger closeHam"}
            onClick={this.toggle}
          >
            <div className="line"></div>
            <div className="line"></div>
            <div className="line"></div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nav;
